*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  background: #0d1d2bc2;
}
.center{
  text-align: center;
}
.center h1{
  font-size: 2.3em;
  color: crimson;
  font-weight: bold;
}
.center h3{
  font-size: 1.4em;
  color: #fff;
  text-decoration: underline 1px solid #555;
}
.container{
  width: 100%;
  margin: 20px auto;
  padding: 30px;
}
.wrapper{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}
.card{
  flex-basis: 30%;
  min-width: 250px;
  height: 200px;
  background: #fff;
  box-shadow: 5px 5px 20px 0 rgba(236,236,236,0.2);
  border-radius: 10px;
  padding: 10px 15px;
  text-align: center;
  margin: 10px auto;
}
.tri{
  background-image: linear-gradient(0deg, #138808 33%, #ffffff 33%, #ffffff 66%, #FF9933 66% );
}
.deaths{
  background: crimson;
  color: #fff;
}
.recovered{
  background: #6fe96f;
}
.active{
  background: #f78e8e;
}
.confirm{
  background: #3cb043;
}
.update{
  background: #ecd81d;
}
.card h3{
  color: rgb(4, 31, 46);
  font-size: 1.8em;
  font-weight: bold;
}
.card p{
  font-size: 2.5em;
  color: rgb(255, 255, 255);
  padding-top: 20px;
  text-shadow: #fc0 1px 0 10px;

}